.products {
	@include flexbox;
	@include justify-content(space-between);
	@include flex-wrap(wrap);
	.products-card {
		width: 23%;
		border: 1px solid #ccc;
		border-radius: 5px;
		margin-top: 20px;
		@include flexbox;
		@include justify-content(space-between);
		.img-product {
			width: 45%;
			padding-right: 5%;
		}
	}
	.ctn-card {
		padding: 20px 20px 10px;
		.info {
			@include flexbox;
			@include justify-content(space-between);
		}
		p {
			padding: 5px 10px;
			&.price {
				color: red;
			}
			&.sales {
				background-color: #165993;
				color: #fff;
			}
			&.old-price {
				text-decoration: line-through;
			}
		}
	}
}
