bmenu li{border-bottom:1px solid #000}.nav-menu li:hover a{text-decoration:underline}.nav-menu li:hover .submenu{display:block}.nav-menu li:hover .submenu a{text-decoration:none}.nav-menu .sub{position:relative}.nav-menu .sub::after{content:">";position:absolute;top:13px;right:0;-webkit-transform:rotate(90deg);transform:rotate(90deg)}.nav-menu .submenu li{display:block}.products{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;-moz-justify-content:space-between;justify-content:space-between;-webkit-flex-wrap:wrap;flex-wrap:wrap}.products .products-card{width:23%;border:1px solid #ccc;border-radius:5px;margin-top:20px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;-moz-justify-content:space-between;justify-content:space-between}.products .products-card .img-product{width:45%;padding-right:5%}.products .ctn-card{padding:20px 20px 10px}.products .ctn-card .info{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;-moz-justify-content:space-between;justify-content:space-between}.products .ctn-card p{padding:5px 10px}.products .ctn-card p.price{color:red}.products .ctn-card p.sales{background-color:#165993;color:#fff}.products .ctn-card p.old-price{text-decoration:line-through}.cart{padding:20px}.cart .count-cart{margin-bottom:20px}
