html {
	font-size: 10px;
	font-family: "Noto Sans JP";
	margin: 0;
	padding: 0;
}

body {
	@include fontsize(1.6);
	margin: 0;
	padding: 0;
	color: $color_primary;
}
a {
	color: $color_link;
	@include transition(0.3s);
	text-decoration: none;
	&:hover {
	  color: $color_link_hover;
	  text-decoration: none;
	}
	&:focus {
	  outline: none;
	  box-shadow: none;
	}
  }

.container{
	width: 1440px;
	margin: 0 auto;
}