.nav-menu {
    @include flexbox;
    li {
        display: inline-block;
        a {
            color: $color_primary;
            padding: 10px 15px;
            display: block;
            
        }
        :hover {
            .submenu {
                display: block;
            }
        }
        .submenu {
            display: none;
            background: #fff;
            li {
                border-bottom: 1px solid #000;
            }
        }
        &:hover {
            a {
                text-decoration: underline;
            }
            .submenu {
                display: block;
                a {
                    text-decoration: none;
                }
            }
        }
    }
    .sub {
        position: relative;
        &::after {
            content: ">";
            position: absolute;
            top: 13px;
            right: 0;
            transform: rotate(90deg);
        }
    }
    .submenu {
        li {
            display: block;
        }
    }
}